<template>

  <div>
    <h3>权限信息编辑</h3>
    <Divider orientation="left">信息填写</Divider>
    <Row>
      <Col :xs="24" :sm="24" :md="12" :lg="12">
        <Form :model="formData" :label-width="80">

          <FormItem label="名称">
            <Input v-model="formData.name" placeholder="请填写名称"></Input>
          </FormItem>

          <FormItem label="状态">
            <Select v-model="formData.status">
              <Option label="禁用" :value="0"></Option>
              <Option label="正常" :value="1"></Option>
            </Select>
          </FormItem>

          <FormItem label="排序">
            <Input v-model="formData.sort" placeholder="请填写排序"></Input>
          </FormItem>

          <FormItem label="路径">
            <Input v-model="formData.path" placeholder="请填写路径"></Input>
          </FormItem>

          <FormItem label="父节点ID">
            <Input v-model="formData.pid" placeholder="请填写父节点ID"></Input>
          </FormItem>

          <FormItem label="icon">
            <Input v-model="formData.icon" placeholder="请填写icon"></Input>
          </FormItem>
          <FormItem label="权限等级">
            <Select v-model="formData.level">
              <Option label="一级权限" :value="1"></Option>
              <Option label="二级权限" :value="2"></Option>
              <Option label="三级权限" :value="3"></Option>
            </Select>
          </FormItem>
          <FormItem label="简介">
            <Input v-model="formData.description" type="textarea" :rows="4" placeholder="请填写简介"></Input>
          </FormItem>
          <FormItem>
            <Button type="success" long @click="submit">提交</Button>
          </FormItem>
        </Form>
        <Spin size="large" fix v-if="load"></Spin>
      </Col>

    </Row>
  </div>
</template>

<script>

import api from './api'

export default {
  name: 'edit',
  data () {
    return {
      formData: {},
      teams: [],
      load: false
    }
  },
  mounted () {
    if (this.$route.query.id != null) {
      this.loadData()
    }
  },
  methods: {
    loadData () {
      this.load = true
      api.detail({ id: this.$route.query.id }, res => {
        this.load = false
        this.formData = res.data.data
      }, () => {
        this.load = false
      })
    },
    submit () {
      if (!this.$formUtil.isStrValid(this.formData.name)) {
        this.$Message.error('请输入名称')
        return
      }
      if (!this.$formUtil.isStrValid(this.formData.status)) {
        this.$Message.error('请选择状态')
        return
      }
      if (!this.$formUtil.isStrValid(this.formData.sort)) {
        this.$Message.error('请输入排序')
        return
      }
      if (!this.$formUtil.isStrValid(this.formData.path)) {
        this.$Message.error('请输入路径')
        return
      }
      if (!this.$formUtil.isStrValid(this.formData.level)) {
        this.$Message.error('请选择权限等级')
        return
      }
      if (!this.$formUtil.isStrValid(this.formData.icon)) {
        this.$Message.error('请输入icon')
        return
      }
      if (!this.$formUtil.isStrValid(this.formData.pid)) {
        this.$Message.error('请输入父节点id')
        return
      }
      if (!this.$formUtil.isStrValid(this.formData.description)) {
        this.$Message.error('请输入简介')
        return
      }
      this.load = true
      api.add(this.formData, () => {
        this.load = false
        // this.$router.push({ name: 'permission' })
      }, () => {
        this.load = false
      })
    }
  }
}
</script>

